const allImgs = document.querySelector('ul.gallery');

//розмітка елемента галереї
function imageTemplate(img) {
    return `<li class="gallery-item">
        <a class="gallery-link" href="${img.original}">
            <img
                class="gallery-image"
                src="${img.preview}"
                alt="${img.description}"
            />
        </a>
    </li>`;
}

function imagesTemplate(imgs) {
    return imgs.map(imageTemplate).join('');
}

    const murkup = imagesTemplate(images);
    allImgs.innerHTML = murkup;

allImgs.classList.add('js-gallery');

const eachLink = document.querySelectorAll('.gallery-link');
eachLink.forEach(e => {
    e.classList.add('js-gallery-link');
});    

const eachImg = document.querySelectorAll('.gallery-image');
eachImg.forEach(e => {
    e.classList.add('js-gallery-image');
});

const lightbox = new SimpleLightbox('.js-gallery a', {
    captions: true,
    captionsData: 'alt',
    captionDelay: 250,
  });





